<template>
  <div class="page">
    <Header>
      <template #left>
        <button @click="callback" class="header__button header__button--back">返回</button>
      </template>
      <template #right>
        <a class="header__button link" href="javascript:void (0);">查看规则</a>
      </template>
    </Header>
    <h1 @click="$router.back()">无效果页</h1>
  </div>
</template>

<script>
import Header from '../components/Header'

export default {
  name: 'test',
  components: {
    Header
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.formPath = from.path
    })
  },
  data() {
    return {
      formPath: null
    }
  },
  methods: {
    callback() {
      if (!this.formPath || this.formPath === '/') {
        this.$router.replace('/')
      } else {
        this.$router.back()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .page {
    text-align: center;
  }

  h1 {
    margin-top: $--header-height;
  }

  .link {
    font-size: rem(14);
    color: #cca352;
  }
</style>
